import { useIsMobile } from '@/order/hooks';
import { ShippingStatusReturnCardProps } from '@/order/types';
import {
  Accordion,
  AccordionSection,
  Card,
  Header,
  Image,
  ContentGrid,
} from '@adobe-commerce/elsie/components';
import { Text } from '@adobe-commerce/elsie/i18n';
import { classes, Slot } from '@adobe-commerce/elsie/lib';
import { FunctionComponent } from 'preact';
import Button from '@adobe-commerce/elsie/components/Button';

export const ShippingStatusReturnCard: FunctionComponent<
  ShippingStatusReturnCardProps
> = ({
  placeholderImage,
  slots,
  collapseThreshold,
  translations,
  returnData,
  routeTracking,
  routeProductDetails,
}) => {
  const isMobile = useIsMobile();

  const ImageTag = routeProductDetails ? 'a' : 'span';

  return (
    <Card
      variant="secondary"
      className={classes([
        'order-shipping-status-card',
        'order-shipping-status-card--return-order',
      ])}
    >
      <Header title={translations.returnOrderCardTitle} />
      {
        <div>
          {returnData?.tracking?.map((track, index) => {
            // No title in return tracking schema for return
            const trackingData = {
              title: '',
              number: track?.trackingNumber ?? '',
              carrier: track?.carrier?.label ?? '',
            };

            const trackingRoute = routeTracking?.(trackingData);
            const handleClick = trackingRoute
              ? () => {
                  window.open(trackingRoute, '_blank', 'noreferrer');
                }
              : null;

            return (
              <div
                key={`${trackingData.number}_${index}`}
                className={'order-shipping-status-card__header'}
              >
                <div>
                  {`${translations.carrier} `}
                  {`${trackingData.carrier?.toLocaleUpperCase()} | `}
                  {trackingData.number}
                </div>
                {handleClick ? (
                  <Button onClick={handleClick}>
                    {translations.trackButton}
                  </Button>
                ) : null}
              </div>
            );
          })}

          {slots?.ReturnItemsDetails ? (
            <Slot
              data-testid="returnItemsDetails"
              name="ReturnItemsDetails"
              slot={slots?.ReturnItemsDetails}
              context={{ items: returnData.items }}
            />
          ) : null}
          <Accordion actionIconPosition="right" data-testid="dropinAccordion">
            <AccordionSection
              defaultOpen={collapseThreshold >= returnData.items.length}
              // @ts-ignore
              title={
                <Text
                  id="Order.ShippingStatusCard.itemText"
                  plural={returnData.items.reduce(
                    (acc, value) => acc + value.requestQuantity,
                    0
                  )}
                  fields={{
                    count: returnData.items.reduce(
                      (acc, value) => acc + value.requestQuantity,
                      0
                    ),
                  }}
                />
              }
            >
              <ContentGrid
                maxColumns={isMobile ? 3 : 9}
                emptyGridContent={<></>}
                className={classes([
                  'order-shipping-status-card__images',
                  ['order-shipping-status-card__images-3', isMobile],
                ])}
              >
                {returnData.items.map((item, index) => {
                  const defaultImageProps = {
                    title: item.thumbnail?.label ?? '',
                    src: item.thumbnail?.url ?? placeholderImage,
                    alt: item.thumbnail?.label ?? '',
                    width: 85,
                    height: 114,
                    loading: 'lazy',
                  } as const;

                  return (
                    <ImageTag
                      key={`${index}${item.uid}`}
                      href={routeProductDetails?.(item) ?? '#'}
                      data-testid={`${index}${item.uid}`}
                    >
                      <Slot
                        slotTag="span"
                        contentTag="span"
                        name="ShippingStatusCardImage"
                        slot={slots?.ShippingStatusReturnCardImage}
                        context={{
                          data: item,
                          defaultImageProps,
                        }}
                      >
                        <Image {...defaultImageProps} />
                      </Slot>
                    </ImageTag>
                  );
                })}
              </ContentGrid>
            </AccordionSection>
          </Accordion>
        </div>
      }
    </Card>
  );
};
