import { ShippingStatusProps } from '@/order/types';
import { classes, Container } from '@adobe-commerce/elsie/lib';
import { CardLoader, ShippingStatusCard } from '@/order/components';
import { useGetStoreConfig, useShippingStatus } from '@/order/hooks';
import { useText } from '@adobe-commerce/elsie/i18n';

export const ShippingStatus: Container<ShippingStatusProps> = ({
  slots,
  className,
  collapseThreshold,
  orderData,
  routeOrderDetails,
  routeTracking,
  routeProductDetails,
}) => {
  const { loading, order, isVirtualProduct } = useShippingStatus({ orderData });
  const storeConfig = useGetStoreConfig();

  const translations = useText({
    carrier: 'Order.ShippingStatusCard.carrier',
    prepositionOf: 'Order.ShippingStatusCard.prepositionOf',
    returnOrderCardTitle: 'Order.ShippingStatusCard.returnOrderCardTitle',
    shippingCardTitle: 'Order.ShippingStatusCard.shippingCardTitle',
    shippingInfoTitle: 'Order.ShippingStatusCard.shippingInfoTitle',
    notYetShippedTitle: 'Order.ShippingStatusCard.notYetShippedTitle',
    noInfoTitle: 'Order.ShippingStatusCard.noInfoTitle',
    returnNumber: 'Order.ShippingStatusCard.returnNumber',
    orderNumber: 'Order.ShippingStatusCard.orderNumber',
    trackButton: 'Order.ShippingStatusCard.trackButton',
  });

  if (!loading && isVirtualProduct) return null;

  const placeholderImage = storeConfig?.baseMediaUrl ?? '';

  return (
    <div className={classes(['order-shipping-status', className])}>
      {!loading && order ? (
        <ShippingStatusCard
          placeholderImage={placeholderImage}
          translations={translations}
          routeOrderDetails={routeOrderDetails}
          routeTracking={routeTracking}
          slots={slots}
          orderData={order}
          collapseThreshold={collapseThreshold}
          routeProductDetails={routeProductDetails}
        />
      ) : (
        <CardLoader withCard={false} />
      )}
    </div>
  );
};
