import { OrderDataModel } from '@/order/data/models';
import { UseShippingStatusProps } from '@/order/types';
import { events } from '@adobe-commerce/event-bus';
import { useState, useEffect } from 'preact/hooks';

export const useShippingStatus = ({ orderData }: UseShippingStatusProps) => {
  const [loading, setLoading] = useState(true);
  const [order, setOrder] = useState(orderData);
  const [isVirtualProduct, setIsVirtualProduct] = useState(false);

  useEffect(() => {
    const event = events.on(
      'order/data',
      (order: OrderDataModel) => {
        setOrder(order);
        setIsVirtualProduct(order?.isVirtual);
        setLoading(false);
      },
      { eager: true }
    );

    orderData?.id && setLoading(false);
    return () => {
      event?.off();
    };
  }, [orderData]);

  return { loading, order, isVirtualProduct };
};
