import { Slot } from '@adobe-commerce/elsie/lib';
import {
  Accordion,
  AccordionSection,
  Card,
  Header,
  Image,
  ContentGrid,
} from '@adobe-commerce/elsie/components';
import { Text } from '@adobe-commerce/elsie/i18n';
import { ShippingStatusCardProps, StatusEnumProps } from '@/order/types';
import '@/order/components/ShippingStatusCard/ShippingStatusCard.css';
import { ShippingStatusReturnCard } from './ShippingStatusReturnCard';
import Button from '@adobe-commerce/elsie/components/Button';
import { FunctionComponent } from 'preact';

export const ShippingStatusCard: FunctionComponent<ShippingStatusCardProps> = ({
  placeholderImage,
  translations,
  slots,
  orderData,
  collapseThreshold = 10,
  routeProductDetails,
  routeTracking,
}) => {
  const isReturnPage = !!orderData?.returnNumber;
  const returnNumber = orderData?.returnNumber;
  const ImageTag = routeProductDetails ? 'a' : 'span';
  const orderStatus = orderData?.status?.toLocaleLowerCase();
  const isSingleShipment = orderData?.shipments?.length === 1;
  const isCompleted =
    isSingleShipment && orderStatus?.includes(StatusEnumProps.COMPLETE);
  const allTrackingIsEmpty = orderData?.shipments?.every(
    (el) => el.tracking.length === 0
  );

  const renderNotYetShippedProduct = orderData?.items?.filter(
    (el) =>
      el?.quantityShipped === 0 || el?.quantityShipped < el?.quantityOrdered
  );

  const totalNotYetShippedProduct = orderData?.items?.reduce((acc, value) => {
    const difference = value.quantityOrdered - value.quantityShipped;
    return acc + (difference > 0 ? difference : 0);
  }, 0);

  // Render shipping status card for return details page
  if (isReturnPage && orderData?.returns.length) {
    const returnData = orderData.returns.find(
      (returnData) => returnData.returnNumber === returnNumber
    );

    if (!returnData || returnData.tracking.length === 0) return null;

    return (
      <ShippingStatusReturnCard
        placeholderImage={placeholderImage}
        slots={slots}
        collapseThreshold={collapseThreshold}
        translations={translations}
        returnData={returnData}
        routeTracking={routeTracking}
        routeProductDetails={routeProductDetails}
      />
    );
  }

  // Hidden if no order data or order is cancelled
  if (!orderData || orderStatus?.includes(StatusEnumProps.CANCELED)) {
    return null;
  }

  // Render static informational card if no shipment created yet (order details page)
  if (!orderData?.shipments?.length) {
    return (
      <Card variant="secondary" className="order-shipping-status-card">
        <Header title={translations.shippingInfoTitle} />
        <div className={'order-shipping-status-card__header'}>
          <div className={'order-shipping-status-card__header--content'}>
            {orderData?.carrier ? <p>{orderData?.carrier}</p> : null}
            <p>{translations.noInfoTitle}</p>
          </div>
        </div>
      </Card>
    );
  }

  if (
    allTrackingIsEmpty &&
    !renderNotYetShippedProduct?.length &&
    isCompleted
  ) {
    return null;
  }

  // Render order shipments details (card per shipment)
  return (
    <>
      {orderData?.shipments?.map(({ tracking, items, id }, index) => {
        const accordionValue = items.reduce((acc, value) => {
          return (
            acc +
            (value?.quantityShipped ?? value.orderItem.quantityShipped ?? 0)
          );
        }, 0);

        return (
          <Card
            key={id}
            variant="secondary"
            className="order-shipping-status-card"
          >
            <Header title={translations.shippingCardTitle} />
            {tracking?.map((track) => {
              const trackingRoute = routeTracking?.(track);
              const handleClick = trackingRoute
                ? () => {
                    window.open(trackingRoute, '_blank', 'noreferrer');
                  }
                : null;

              return (
                <div
                  className={'order-shipping-status-card__header'}
                  key={track.number}
                  role={'status'}
                >
                  <div
                    className={'order-shipping-status-card__header--content'}
                  >
                    <p>
                      {translations.carrier}{' '}
                      {track?.carrier?.toLocaleUpperCase()} | {track?.number}
                    </p>
                    <p>{track?.title}</p>
                  </div>
                  {slots?.DeliveryTrackActions ? (
                    <Slot
                      data-testid="deliverySlotActions"
                      name="DeliveryTrackActions"
                      slot={slots?.DeliveryTrackActions}
                      context={{ trackInformation: track }}
                    />
                  ) : handleClick ? (
                    <Button onClick={handleClick}>
                      {translations.trackButton}
                    </Button>
                  ) : null}
                </div>
              );
            })}

            {!isCompleted ? (
              <Accordion
                actionIconPosition="right"
                data-testid="dropinAccordion"
              >
                <AccordionSection
                  data-position={index + 1}
                  defaultOpen={collapseThreshold >= items?.length}
                  // @ts-ignore
                  title={
                    <Text
                      id="Order.ShippingStatusCard.notYetShippedImagesTitle"
                      plural={accordionValue}
                      fields={{ count: accordionValue }}
                    />
                  }
                >
                  <ContentGrid
                    maxColumns={6}
                    emptyGridContent={<></>}
                    className={'order-shipping-status-card__images'}
                  >
                    {items?.map((item) => {
                      const label =
                        item?.orderItem?.product?.thumbnail?.label ?? '';

                      const defaultImageProps = {
                        title: label,
                        src:
                          item?.orderItem?.product?.thumbnail?.url ??
                          placeholderImage,
                        alt: label,
                        width: 85,
                        height: 114,
                        loading: 'eager',
                      } as const;

                      return (
                        <ImageTag
                          key={item.id}
                          href={routeProductDetails?.(item) ?? '#'}
                        >
                          <Slot
                            slotTag="span"
                            contentTag="span"
                            name="ShippingStatusCardImage"
                            slot={slots?.ShippingStatusCardImage}
                            context={{
                              data: item,
                              defaultImageProps,
                            }}
                          >
                            <Image {...defaultImageProps} />
                          </Slot>
                        </ImageTag>
                      );
                    })}
                  </ContentGrid>
                </AccordionSection>
              </Accordion>
            ) : null}

            {slots?.DeliveryTimeLine ? (
              <Slot
                data-testid="deliverySlotTimeLine"
                name="DeliveryTimeLine"
                slot={slots?.DeliveryTimeLine}
                context={{}}
              />
            ) : null}
          </Card>
        );
      })}
      {renderNotYetShippedProduct?.length ? (
        <Card
          variant="secondary"
          className="order-shipping-status-card"
          data-testid="dropinAccordionNotYetShipped2"
        >
          <Header title={translations.notYetShippedTitle} />

          <Accordion actionIconPosition="right">
            <AccordionSection
              defaultOpen={
                collapseThreshold >= renderNotYetShippedProduct?.length
              }
              // @ts-ignore
              title={
                <Text
                  id="Order.ShippingStatusCard.notYetShippedImagesTitle"
                  plural={totalNotYetShippedProduct}
                  fields={{
                    count: totalNotYetShippedProduct,
                  }}
                />
              }
            >
              <ContentGrid
                maxColumns={6}
                emptyGridContent={<></>}
                className={'order-shipping-status-card__images'}
              >
                {renderNotYetShippedProduct?.map((item) => {
                  const defaultImageProps = {
                    title: item.thumbnail?.label ?? '',
                    src: item.thumbnail?.url ?? '',
                    alt: item.thumbnail?.label ?? '',
                    width: 85,
                    height: 114,
                    loading: 'lazy',
                  } as const;

                  return (
                    <ImageTag
                      key={item.id}
                      href={routeProductDetails?.(item) ?? '#'}
                    >
                      <Slot
                        slotTag="span"
                        contentTag="span"
                        name="NotYetShippedProductImage"
                        slot={slots?.NotYetShippedProductImage}
                        context={{
                          data: item,
                          defaultImageProps,
                        }}
                      >
                        <Image {...defaultImageProps} />
                      </Slot>
                    </ImageTag>
                  );
                })}
              </ContentGrid>
            </AccordionSection>
          </Accordion>
        </Card>
      ) : null}
    </>
  );
};
